import { BASE_IMAGE_URL,getGoodsList } from '@/utils/api'
import uniLoadMore from '@/components/uni-load-more.vue';

export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			loadingText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			list: [],
			loadingType: 0,
			page: 1,
			keyword: ''
		}
	},
	methods: {
		back: function(){
			uni.navigateBack({
				delta: 1
			});
		},
		async search(){
			let res = await getGoodsList('',this.page, 1, this.keyword);
			uni.stopPullDownRefresh();
			if(res.code == 1000){
				if(this.page == 1){
					this.list = res.data;
				}else{
					this.list = this.list.concat(res.data);
				}
				
				if(res.data.length < 10){
					this.loadingType = 2;
				}else{
					this.loadingType = 0;
				}
			}
		}
	},
	onLoad() {
	},
	onReachBottom() {
		this.loadingType = 1;
		this.page++;
		this.search();
	},
	
	onPullDownRefresh() {
		console.log("dddddd");
		this.page = 1;
		this.list = [];
		this.search();
	},
}
